/* ****************************************************************************************************
            DESTINATION
**************************************************************************************************** */

.small-hero {
    width: 100%;
    height: 50vh;
    -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.small-hero .bg {
    width: 100%;
    height: 50vh;
    -o-object-fit: conver;
       object-fit: conver;
}

.small-hero .title {
    color: white;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    line-height: 50vh;
    text-align: center;
    font-size: 8em;
}

.small-hero .subtitle {
    color: white;
    position: absolute;
    top: 25vh;
    left: 0;
    right: 0;
    bottom: 0;
    line-height: 25vh;
    text-align: center;
    font-size: 3em;
}

.destination .section {
    width: 100%;
    content: "";
    display: table;
    clear: both;
    -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    /* padding: 2em 2em 0 2em; */
    margin-bottom: 2em;
    border-radius: 3px;
}

.destination .section .content {
    height: 100%;
    display: block;
    line-height: 1.6em;
}

.destination .section .card {
    width: 400px;
    height: 300px;
    position: relative;
    margin: 2em;
    float: left;
}

.destination .section:nth-child(even) .card {
    float: right;
}

.destination .section.left .card {
    float: left;
}

.destination .section .card img {
    -o-object-fit: cover;
       object-fit: cover;
    width: 100%;
    height: 100%;
    object-fit: cover;
    -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    border-radius: 3px;
}

.destination .section .card .desc {
    cursor: pointer;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-transition: 0.3s ease;
    -o-transition: 0.3s ease;
    transition: 0.3s ease;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.8);
    text-decoration: none;
    color: white;
    font-size: 2em;
    border-radius: 3px;
}

.destination .content {
    margin: 2em;
}

.destination .section .card .desc:hover {
    opacity: 1;
}

.destination .section .card .desc * {
    position: absolute;
    top: 50%;
    right: 0.5em;
    left: 0.5em;
    text-align: center;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    margin: 0 auto;
}


